os buenos")]),_c('p',[_vm._v("Este aspecto aplica para los productos que requieren el registro o visto bueno de algunas autoridades especiales en el territorio nacional. Este requisito depende de las características del producto a exportar y los trámites respectivos se realizan a través de la Ventanilla Única de Comercio Exterior (VUCE). ")])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Carta de responsabilidad "}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{staticClass:"img-300",attrs:{"src":require("@/assets/tema1/img-24.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-7 mb-4 mb-md-0"},[_c('h4',[_vm._v("Carta de responsabilidad ")]),_c('p',[_vm._v("Es un documento clave en el proceso de exportación y su objetivo es acompañar las mercancías durante su desplazamiento en territorio nacional. En este caso, los exportadores certifican ante la Policía, en la Ventanilla Única de Comercio Exterior – VUCE, que las mercancías objeto que salen del país no llevan ningún tipo de contaminación, armas, explosivos e inclusive precursores químicos que sean considerados o tipificados como estupefaciente o elementos restrictivos. ")]),_c('p',[_vm._v("Todas las mercancías exportadas desde Colombia están sometidas o sujetas a la inspección de las autoridades, que determinará el método a utilizar de los disponibles: inspecciones físicas, con biosensores (como el uso de perros entrenados) o mediante escáneres. ")])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Certificado de origen"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{staticClass:"img-300",attrs:{"src":require("@/assets/tema1/img-2.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-7 mb-4 mb-md-0 align-self-center"},[_c('h4',[_vm._v("Certificado de origen")]),_c('p',[_vm._v("Los bienes con certificado de origen deben cumplir la condición de ser origin